left: 0;\n}\n\n/* ===================================== comman styling ends ================================ */\n\n/* =================================== left-side-column ===================================== */\n\n.left-side-col {\n  background-color: #272935;\n  padding: 0 2px 0 10px;\n  border-color: #2e313a;\n  color: #fff;\n  /* max-height: 700px;\n  overflow: auto; */\n}\n\n#home {\n  padding: 0;\n}\n\n#home ul li a span {\n  font-size: 12px;\n  line-height: 2.5;\n  color: #fff;\n}\n\n#home ul li a span.float-right {\n  color: #33ae72;\n}\n\n#home ul li.red a span.float-right {\n  color: #ee6560;\n}\n\n#home li:hover {\n  background-color: #2c2f3a;\n}\n\n#home li:hover a {\n  cursor: pointer;\n}\n\n#menu1 {\n  padding: 0;\n}\n\n#menu1 ul li a span {\n  font-size: 12px;\n  line-height: 2.5;\n  color: #fff;\n}\n\n#menu1 ul li a span.float-right {\n  color: #33ae72;\n}\n\n#menu1 ul li.red a span.float-right {\n  color: #ee6560;\n}\n\n#menu1 li:hover {\n  background-color: #2c2f3a;\n}\n\n#menu1 li:hover a {\n  cursor: pointer;\n}\n\n#menu2 {\n  padding: 0;\n}\n\n#menu2 ul li a span {\n  font-size: 12px;\n  line-height: 2.5;\n  color: #fff;\n}\n\n#menu2 ul li a span.float-right {\n  color: #33ae72;\n}\n\n#menu2 ul li.red a span.float-right {\n  color: #ee6560;\n}\n\n#menu2 li:hover {\n  background-color: #2c2f3a;\n}\n\n#menu2 li:hover a {\n  cursor: pointer;\n}\n\n/* ********** nav tabs starts ************* */\n\n.nav-tabs {\n  border-bottom: 1px solid #2e313a;\n}\n\n.nav-tabs .nav-link {\n  padding: 0 6px;\n  font-size: 12px;\n  color: #7c88a0;\n}\n\n.nav-tabs .nav-link.active {\n  font-weight: 500;\n  color: #5b90e4;\n  border-bottom: 1px solid #5b90e4;\n}\n\n/* ********** nav tabs ends ************* */\n\n/* ******************* seacr-bar ************** */\n\n.search-bar input {\n  width: 100% !important;\n  margin-top: 20px;\n  height: 30px;\n  border-radius: 4px;\n  padding-left: 25px;\n  border: 1px solid #272935;\n  box-sizing: border-box;\n  position: relative;\n  background-color: #363943;\n}\n\n/* for fancy scroll bar */\n\n::-webkit-scrollbar-track {\n  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);\n  border-radius: 10px;\n  background-color: #272935;\n}\n\n::-webkit-scrollbar {\n  width: 10px;\n  background-color: #272935;\n}\n\n::-webkit-scrollbar-thumb {\n  border-radius: 10px;\n  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);\n  background-color: #363943;\n}\n\n.left-side-tab ul li {\n  padding-right: 6px;\n}\n\n/* =================================== left-side-column =================================== */\n\n/* tabs */\n\n.left-side-tab ul {\n  padding: 0;\n  max-height: 550px;\n  overflow-y: auto;\n}\n\n/* =================================== body styling ends ================================== */\n\n/* ========================== right-side-bottom styling strts ========================= */\n\n#buy-sell-ltc {\n  overflow: hidden;\n  /* float: left;\n  width: 62.7%; */\n  /* min-width: 552px; */\n  height: 325px;\n  box-sizing: border-box;\n  padding-right: 5px;\n  background-color: #31343d;\n  margin-top: 15px;\n}\n\n.buy-sell-ltc {\n  position: relative;\n  width: 100%;\n  height: 100%;\n}\n\n.buy-sell-ltc .trade {\n  box-sizing: border-box;\n  float: left;\n  width: 50%;\n}\n\n.buy-sell-ltc .trade-content {\n  overflow: hidden;\n  box-sizing: border-box;\n  width: 100%;\n  height: 360px;\n  padding: 20px 18px 39px 20px;\n  background-color: #272935;\n  text-align: left;\n}\n\n.buy-sell-ltc .buyIn {\n  padding-right: 20px;\n  border-right: 1px solid #2e313a;\n}\n\n.buy-sell-ltc .trade h3 {\n  margin: 0 0 10px;\n  font-size: 14px;\n  font-weight: 500;\n  line-height: 20px;\n}\n\n.greenFont {\n  color: #33ae72;\n}\n\n.buy-sell-ltc .trade .input-text {\n  position: relative;\n}\n\n.buy-sell-ltc .trade .input-text input {\n  height: 37px;\n  width: 100%;\n  box-sizing: border-box;\n  border-radius: 2px;\n  font-size: 14px;\n  text-indent: 10px;\n  background-color: #363943;\n  border: 0;\n  color: #cd