sizing: border-box;\n  width: 100%;\n  height: 36px;\n  margin-top: 15px;\n  font-size: 16px;\n  line-height: 10px;\n  font-weight: 500;\n  text-align: center;\n  color: #fff;\n  border-radius: 2px;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n  border: 0;\n}\n\n.buy-sell-ltc .buyOut {\n  padding-left: 20px;\n}\n\n.redFont {\n  color: #ee6560;\n}\n\n.buy-sell-ltc .trade .btn-red {\n  border-radius: 2px;\n  background-color: #ee6560;\n}\n\n.buy-sell-ltc .trade .btn-red:hover {\n  background-color: #c14e56;\n  border: 2px solid rgba(5, 5, 5, 0.08);\n}\n\n.buy-sell-ltc .trade .btn-red:active {\n  background-color: #a33c43;\n  border: 2px solid rgba(5, 5, 5, 0.08);\n}\n\n.trade-content ul li label {\n  display: block;\n}\n\n/* ========================== right-side-bottom styling ends ========================= */\n\n/* ========================== order book styling ends ========================= */\n\n#order-book {\n  /* float: right;\n  width: 37.3%;\n  min-width: 329px; */\n  height: 325px;\n  background-color: #272935;\n}\n\n.order-book-container {\n  padding-left: 0;\n  margin-top: 15px;\n}\n\n.order-book-cont {\n  width: 100%;\n  height: 100%;\n  padding: 10px 0 20px;\n}\n\n.order-book-cont dl {\n  margin: 0;\n}\n\n.order-book-cont dd,\n.order-book-cont dt {\n  padding: 0 20px;\n  margin: 0 0 5px;\n}\n\n.order-book-cont dd .inner,\n.order-book-cont dt .inner {\n  cursor: pointer;\n  width: 100%;\n  display: flex;\n  justify-content: space-between;\n}\n\n.order-book-cont dd .inner .layer,\n.order-book-cont dd .inner .price,\n.order-book-cont dt .inner .layer,\n.order-book-cont dt .inner .price {\n  flex: 1;\n}\n\n.order-book-cont dd .inner span,\n.order-book-cont dt .inner span {\n  font-size: 12px;\n  line-height: 20px;\n  color: #cdd1ea;\n  text-align: left;\n}\n\n.order-book-cont dt .inner span {\n  color: #7c88a0;\n}\n\n.order-book-cont dd .inner .amount,\n.order-book-cont dt .inner .amount {\n  flex: 1;