type,\n.tradesWrap dt .inner span:first-of-type {\n  min-width: 90px;\n}\n\n.tradesWrap dd .inner .type,\n.tradesWrap dt .inner .type {\n  flex: 1;\n}\n\n.tradesWrap dt .inner span {\n  color: #7c88a0;\n}\n\n.tradesWrap dd .green .type,\n.tradesWrap dt .green .type {\n  color: #33ae72;\n}\n\n/* ======================== market trades styling ends =================== */\n\n/* ========================== coine detail section styling starts ======================= */\n\n#coin-detail {\n  float: left;\n  width: 100%;\n  height: auto;\n  margin-top: 5px;\n  background-color: #272935;\n}\n\n.coinDetail {\n  width: 100%;\n  height: 100%;\n}\n\n.coinDetail .coinDetailCon {\n  width: 100%;\n  height: 100%;\n  padding: 10px;\n  padding-bottom: 0;\n  box-sizing: border-box;\n  color: #7c88a0;\n  font-size: 14px;\n  text-align: left;\n}\n\n.coinDetail .coinDetailCon li {\n  width: 100%;\n  line-height: 40px;\n  height: 40px;\n  border-bottom: 1px solid #2e313a;\n  display: flex;\n}\n\n.coinDetail .coinDetailCon li .white {\n  color: #cdd1ea;\n}\n\n.coinDetail .coinDetailCon .ConText {\n  height: auto;\n  width: 100%;\n  text-align: left;\n}\n\n.coinDetail .coinDetailCon .ConText div {\n  margin-top: 20px;\n  font-size: 18px;\n  line-height: 20px;\n  height: 20px;\n  color: #c7cce6;\n}\n\n.coinDetail .coinDetailCon .ConText p {\n  margin-top: 10px;\n  margin-bottom: 10px;\n  font-size: 12px;\n  word-spacing: 2px;\n  letter-spacing: 1px;\n}\n\n.coinDetail .coinDetailCon li div {\n  width: 49%;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  word-wrap: nowrap;\n}\n\n.coinDetail .coinDetailCon li div span {\n  display: block;\n  float: left;\n  width: 140px;\n}\n\n.coinDetail .coinDetailCon li div a {\n  color: #5b90e4;\n}\n\n.coinDetail .coinDetailCon li:last-of-type {\n  border: 0;\n}\n\n/* ========================== coine detail section styling ends ======================= */\n\n/* ======================== footer styling strts ========================================== */\n\n#